.logo-container {
    position: absolute;
    width: 220px;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-accent);
    left: calc(50% - 100px);
    border-radius: 5px;

    @media (min-width: 750px) {
        width: 300px;
        height: 110px;
        left: calc(50% - 150px);
    }

    &.inline {
        position: unset;
        margin: 0 auto;
    }
}

.logo {
    width: 200px;

    @media (min-width: 750px) {
        width: 300px;
    }
}